@import url(https://fonts.googleapis.com/css2?family=Texturina:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display+SC:ital,wght@0,400;0,700;0,900;1,400;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  color: white;
  text-align: center;
  margin: 0px;
  padding: 0px;
}

*::-webkit-scrollbar {
  display: none;
}

.page,
.housePage,
.slytherinHousePage,
.gryffindorHousePage,
.hufflepuffHousePage,
.ravenclawHousePage {
  overflow-x: hidden;
  height: 100vh;
  width: 100vw;
  scroll-snap-align: start;
  -ms-scroll-snap-type: block mandatory;
      scroll-snap-type: block mandatory;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  position: relative;
}

.fullscreen {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.houseIcon {
  width: 6rem;
  fill: currentColor;
  object-fit: contain;
}

.houseSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  padding: 1rem;
  font-family: 'Texturina', serif;
}

.houseSection::before {
  content: '';
  width: 100%;
  height: 200px;
  background: -webkit-linear-gradient(
    bottom,
    rgba(13, 13, 55, 0) 0%,
    #000000 100%
  );
  position: absolute;
  top: 0;
}

.housePage::after {
  content: '';
  width: 100%;
  height: 200px;
  background: -webkit-linear-gradient(
    top,
    rgba(13, 13, 55, 0) 0%,
    #000000 100%
  );
  position: absolute;
  left: 0;
  bottom: -100%;
}

.houseSection .houseName {
  font-size: 2.25rem;
  line-height: 1;
  margin: 1rem 0;
  letter-spacing: 0.25rem;
  text-transform: uppercase;
}

.houseSection .houseTraits {
  font-weight: 100;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  margin: 1rem 0;
  letter-spacing: 0.3rem;
  font-size: 1.2rem;
  text-transform: lowercase;
}

.quote {
  margin: 2rem auto;
  max-width: 16rem;
}

.quote .quotationMark {
  font-family: 'Times New Roman', Times, serif;
  font-size: 3.75rem;
  line-height: 1;
}

.quote h2 {
  font-style: italic;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  font-size: large;
  margin-bottom: 1.5rem;
}

.quote .sortingHat {
  font-style: normal;
  font-weight: lighter;
  letter-spacing: 0.5rem;
  text-transform: uppercase;
}

.founder {
  min-height: 100vh;
}

.founderSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 1rem;
  perspective: 1000px;
}

.founderCard {
  max-width: 50%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  transform-style: preserve-3d;
}

.founderCard img {
  max-height: 50vh;
  margin: auto;
  transition: all 0.5s ease-out;
}

.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  min-height: 35vh;
}

.founderData h1 {
  font-family: 'Texturina', serif;
  font-size: large;
  font-weight: lighter;
}

.dropcap {
  font-size: 3rem;
  font-weight: bold;
  line-height: 1;
  margin-right: 0.5rem;
}

.decor {
  position: absolute;
  width: 100%;
  height: 100%;
}

.block {
  margin-top: 30vh;
  height: 70vh;
}

.snake {
  width: auto;
  height: 50vh;
  padding: 1rem;
  margin-top: -60vh;
  opacity: 0.1;
  transform: scaleX(-1) rotate(-33deg);
}

.eagle {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: -10vh;
  margin-left: -30vw;
  opacity: 0.1;
}

.lion {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.1;
  margin-top: 40vh;
}

.badger {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.1;
  height: 40vh;
  margin-top: 70vh;
}

.lineblock {
  background-color: rgb(0, 24, 0);
  height: 100vh;
  width: 50vw;
  margin-left: 25vw;
}

.linesnake {
  margin-top: -100vh;
  width: 80vw;
  opacity: 0.1;
  object-fit: contain;
  transform: rotate(120deg);
}

.background {
  object-fit: cover;
  object-position: center;
}

@media (min-width: 1024px) {
  .founderSection {
    flex-direction: row;
    padding: 1rem 16rem;
  }

  .dropcap {
    font-size: 4rem;
  }

  .founderData {
    margin: 2rem;
    text-align: left;
  }

  .founderData h1 {
    text-align: left;
    font-size: x-large;
    padding: 1rem;
  }

  .container {
    min-height: 40vh;
  }

  .characterSection {
    flex-direction: row;
    padding: 1rem 10rem;
  }

  .characterDetails h1 {
    text-align: left;
    font-size: 4rem;
  }

  .characterDetails h2 {
    text-align: left;
    font-size: x-large;
  }

  .characterCard {
    margin-top: 0;
    margin-left: 4rem;
  }

  .characterCard img {
    max-height: 70vh;
  }

  .snake {
    transform: scaleX(1) rotate(-35deg);
    position: absolute;
    height: 100vh;
    margin-top: -5%;
    margin-left: -18%;
    top: 0;
    left: 0;
  }

  .eagle {
    position: absolute;
    height: 80vh;
    top: 0;
    left: 0;
    opacity: 0.1;
    margin-top: 6%;
  }

  .lion {
    height: 100vh;
    margin: 0;
  }

  .badger {
    margin-top: 0;
    height: 80vh;
  }

  .block {
    position: absolute;
    margin-top: 0;
    width: 45%;
    height: 100vh;
    left: 0;
  }

  .linesnake {
    display: none;
  }

  .lineblock {
    display: none;
  }
}

.green {
  color: forestgreen;
}

.bggreen::after {
  color: forestgreen;
}

.darkgreen {
  background-color: rgb(0, 24, 0);
}

.blue {
  color: rgb(0, 81, 255);
}

.bgblue::after {
  background-color: rgb(0, 81, 255);
}

.darkblue {
  background-color: rgb(0, 15, 46);
}

.red {
  color: rgb(168, 0, 0);
}

.darkred {
  background-color: rgb(60, 0, 0);
}

.bgred::after {
  background-color: rgb(168, 0, 0);
}

.yellow {
  color: orange;
}

.bgyellow:after {
  background-color: orange;
}

.darkyellow {
  background-color: rgb(80, 43, 0);
}

.slytherin {
  background-image: url(/hogwarts-website/static/media/slytherin.d5b266b7.png);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

.ravenclaw {
  background-image: url(/hogwarts-website/static/media/ravenclaw.c354cb08.png);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

.gryffindor {
  background-image: url(/hogwarts-website/static/media/gryffindor.9b8b789a.png);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

.hufflepuff {
  background-image: url(/hogwarts-website/static/media/hufflepuff.f58c5318.png);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

.opacity-10 {
  opacity: 0.1;
}

.z-10 {
  z-index: 10;
}

.z-20 {
  z-index: 20;
  position: absolute;
  animation: float 4s ease-in-out infinite;
}

.z-30 {
  z-index: 30;
  position: absolute;
  animation: float 4s ease-in-out infinite reverse;
}

@keyframes float {
  0% {
    transform: translate(0%, 0%);
  }
  50% {
    transform: translate(0%, -4%);
  }
  100% {
    transform: translate(0%, 0%);
  }
}

